.popup {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  min-width: 320px;
  padding: 30px;
  background: rgba(black, .8);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  overflow: auto;
  z-index: 999;
  transition: all .4s;
  &.visible {
    pointer-events: all;
    visibility: visible;
    opacity: 1;
    transition: all .6s; }
  &.animation &__wrap {
    opacity: 1;
    transform: scale(1); }
  &__wrap {
    position: relative;
    max-width: 800px;
    width: 100%;
    margin: auto;
    opacity: 0;
    transform: scale(.8);
    transition: opacity 1s, transform 1s;
    border: 5px solid $green;
    background-color: black;
    font-size: 0;
    iframe {
      width: 100%;
      @include t {
        height: 400px; }
      @include m {
        height: 300px; }
      @include s {
        height: 200px; } } }
  &__close {
    position: absolute;
    top: -30px;
    right: -30px;
    width: 20px;
    height: 20px;
    font-size: 0;
    @include t {
      right: -5px; }
    .icon {
      font-size: 20px;
      fill: $green;
      transition: all .3s; }
    &:hover .icon {
      fill: $white; } }
  &_red &__wrap {
    border-color: $red; }
  &_red &__close .icon {
    fill: $red;
    &:hover {
      fill: $white; } } }





